import { Component } from '@angular/core';
import { PopoverController, AlertController } from '@ionic/angular';
import { AccountPopoverComponent } from '../../components/account-popover/account-popover.component';
import { DatapointsPopoverComponent } from '../../components/datapoints-popover/datapoints-popover.component';
import {DatapointsService} from '../../services/datapoints/datapoints.service';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { ToastController, Platform } from '@ionic/angular';
import * as moment from 'moment';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';

@Component({
    selector: 'app-datapoints',
    templateUrl: 'datapoints.page.html',
    styleUrls: ['datapoints.page.scss'],
})
export class DatapointsPage {
    
    public properties:any;
    
    public datapoints:Array<any>;
    public hiddenDatapointIds:Array<any>;
    public selectedHiddenDatapointIds:Array<any>;
    public unselectedHiddenDatapointIds:Array<any>;
    
    public inCooldown:boolean;     
    
    public isForbidden:boolean;
    public isIos:boolean;
    
    constructor(public popoverController: PopoverController, public alertController: AlertController, 
        private datapointsService:DatapointsService, private authenticationService: AuthenticationService, 
        private router: Router, public toastController: ToastController, private iab: InAppBrowser, private platform:Platform) {
        
        this.properties = {loading:true, toggleHide:false, displayType:"list"}
        
        
        this.isIos = this.platform.is('ios');        
        
        this.datapoints = [];
        this.getDatapoints();
        
        
        this.hiddenDatapointIds = [];
        this.selectedHiddenDatapointIds = [];
        this.unselectedHiddenDatapointIds = [];
        this.getHiddenDatapointIds();
        
        
        this.datapointsService.monitorDatapoints().subscribe(value => {
            if (value){
                this.getDatapointValues();
            }
        });
        
        setTimeout(() => {
            this.getDatapointValues();
        }, 300000);       
        
        this.inCooldown = false;
        this.isForbidden = false;
        
        
        
        
    }
    
    
    public add(datapoint){
        
        if (this.inCooldown){
            this.inCooldownError();
            return;
        }
        
        this.inCooldown = true;
        setTimeout(() => {
            this.inCooldown = false;
        },1000);        
        
        let timeout = setTimeout(() => {
  
            this.datapointsService.addValue(datapoint, {value: 1}).then((data:any) => {

            }).catch((e) => {

                if (e.status === 401){
                    this.refreshTokenAdd(datapoint);
                }
                else{ 
                    datapoint.value -= 1;
                    this.getDatapointValues();                    
                    this.valueAddedError();           
                }

            }); 
        
        },10000);
        
        
        datapoint.value += 1;
        datapoint.last_added = new Date();
        this.valueAdded(datapoint, timeout);         
        
        
    }
    
    
    
    public refreshTokenAdd(datapoint){
        
        this.authenticationService.refreshToken().then(() => {
            
            this.datapointsService.addValue(datapoint, {value: 1}).then((data:any) => {
                //datapoint.value += 1;
                //datapoint.last_added = new Date();
                //this.valueAdded(datapoint);
            }).catch(() => {
                datapoint.value -= 1;
                datapoint.value -= 1;
                this.getDatapointValues();
            })
            
        }).catch(() => {
            this.valueAddedError(); 
            this.router.navigate(['login']);
        });         
        
       
    }
    
    
    async valueAdded(datapoint, timeout) {
        const toast = await this.toastController.create({
            message: 'Changes Saved',
            duration: 10000,
            color:'dark',
            mode:'md',
            buttons: [
            
                {
                    side: 'end',
                    text: 'Undo',
                    handler: () => {
                        clearTimeout(timeout);
                        datapoint.value -= 1;
                        this.getDatapointValues();
                                              
                    }
                }
                
            ]
        });
        toast.present();
    }    
    
    
    async valueAddedError() {
        const toast = await this.toastController.create({
            message: 'Error adding value. Try again soon.',
            duration: 2000,
            color:'danger',
            mode:'md',
            buttons: []
        });
        toast.present();
    }
    
    async inCooldownError() {
        const toast = await this.toastController.create({
            message: 'Wait 1 second to add value again',
            duration: 1000,
            color:'warning',
            mode:'md',
            buttons: []
        });
        toast.present();
    }      
    
        
    public getDatapoints(){
        
        this.isForbidden = false;
        
        this.datapointsService.getDatapoints().then((data:Array<any>) => {

            
            this.datapoints = data;
            
            
            this.getDatapointValues();
            
            this.properties.loading = false;
            
        }).catch((e) => {
            if (e && e.status === 401){
                
                this.refreshToken();
                
            }
            else if (e && e.status === 403){
                this.properties.loading = false;
                this.isForbidden = true;
            }
            else{
                this.properties.loading = false;
                this.datapointsError();
            }
        })
    }
    
    async datapointsError() {
        const toast = await this.toastController.create({
            message: 'Error retreiving data',
            duration: 4000,
            color:'danger',
            mode:'md',
            buttons: []
        });
        toast.present();
    }    
    
    private getDatapointValues(){
        if (this.datapoints.length > 0){
            let index = 0;
            this.setDatapointValueCallback(index);
        }
    }
    
    
    private setDatapointValueCallback(index){
        this.setDatapointValue(index).then(() => {
            index += 1;
            if (index <= (this.datapoints.length - 1)){
                this.setDatapointValueCallback(index);
            }
        }).catch(() => {
            
        })
    }
    
    
    private setDatapointValue(datapointIndex){
        return new Promise((resolve, reject) => {
            this.datapointsService.getValues(this.datapoints[datapointIndex]).then((data:Array<any>) => {

                
                
                if (data && data.length > 0){
                    this.datapoints[datapointIndex].last_added = data[data.length - 1].date;
                    
                    let value = 0;
                    
                    for (let valueEntry of data){
                        value += 1 /*valueEntry.value*/;
                    }
                    
                    this.datapoints[datapointIndex].value = value;
                    
                }
                else{
                    this.datapoints[datapointIndex].value = 0;                   
                }
                
                
                setTimeout(() => {
                    resolve(data); 
                },100);
                
            }).catch((e) => {
                reject(e);
            })
        });    
    }
    
    
    private refreshToken(){
        this.authenticationService.refreshToken().then(() => {

            this.datapointsService.getDatapoints().then((data:Array<any>) => {

                this.datapoints = data;
                this.getDatapointValues();
                this.properties.loading = false;
            });
            
        }).catch(() => {
            this.router.navigate(['login']);
        });        
    }
    
    public toggleDatapoint(datapoint){
        
        let index = this.hiddenDatapointIds.indexOf(datapoint.timeSeriesId);
        if (index > -1){
            //show datapoint
            this.hiddenDatapointIds.splice(index,1);
            this.unselectedHiddenDatapointIds.push(datapoint.timeSeriesId);
        }
        else{
            //hide datapoint
            this.hiddenDatapointIds.push(datapoint.timeSeriesId);
            this.selectedHiddenDatapointIds.push(datapoint.timeSeriesId);            
        }
        

    }
    
    public saveHiddenDatapoints(){
        this.properties.toggleHide = false; 
        this.selectedHiddenDatapointIds = [];
        this.unselectedHiddenDatapointIds = [];
        this.datapointsService.setHiddenDatapointIds(this.hiddenDatapointIds).then(() => {}).catch(() => {})         
    }
    
    public cancelHiddenDatapoints(){
        this.properties.toggleHide = false; 
        
        //datapoint was hidden
        //made it unhidden
        //pressed cancel
        //it should go back to being hidden, however it remains unhudden
        
        
        for (let id of this.selectedHiddenDatapointIds){
            let index = this.hiddenDatapointIds.indexOf(id);
            if (index > -1){
                this.hiddenDatapointIds.splice(index,1);
            }
        }
        this.selectedHiddenDatapointIds = [];
        
        for (let id of this.unselectedHiddenDatapointIds){
            let index = this.hiddenDatapointIds.indexOf(id);
            if (index < 0){
                this.hiddenDatapointIds.push(id);
            }
        }
        this.unselectedHiddenDatapointIds = [];        
        
    }   
    
    private getHiddenDatapointIds(){
        this.datapointsService.getHiddenDatapointIds().then((data:Array<number>) => {
            this.hiddenDatapointIds = data;
        }).catch(() => {
            
        }) 
    }
    
    
    public changeDisplayType(){
       if (this.properties.displayType === "list"){
           this.properties.displayType = "grid";
       }
       else{
           this.properties.displayType = "list"
       }
    }
    
    
    async accountPopover(ev: any) {
        const popover = await this.popoverController.create({
            component: AccountPopoverComponent,
            event: ev,
            translucent: true,
            mode: "md"
        });
        
        popover.present();
        
        return popover.onDidDismiss().then((data) => {

            
            if (data && data.data && data.data.data === "settings"){
                this.router.navigate(['user-settings']);
            }
            else if (data && data.data && data.data.data === "logout"){
                this.logout();
            }
            
        })
    }    
    
    
    async logout() {
       const alert = await this.alertController.create({
           header: 'Confirm',
           subHeader: 'Are you sure you want to logout?',
           buttons: [
               {
                   text: 'Cancel',
                   role: 'cancel',
                   cssClass: 'secondary',
                   handler: () => {
                   }
               }, {
                   text: 'Logout',
                   handler: (data) => {
                       this.authenticationService.logout();
                   }
               }
           ],
           mode: "md"
       });

       await alert.present();
     }      
    
    

    async dataPointsPopover(ev: any) {
        const popover = await this.popoverController.create({
            component: DatapointsPopoverComponent,
            event: ev,
            translucent: true,
            mode: "md"
        });
        
        popover.present();
        
        return popover.onDidDismiss().then((data) => {

            
            if (data && data.data && data.data.data === "showhide"){
                
                if (!this.properties.toggleHide){
                    this.properties.displayType = "list";
                    this.properties.toggleHide = true;                    
                }
                else{
                    this.properties.toggleHide = false; 
                }

            }
            
            else if (data && data.data && data.data.data === "adddatapoint"){
                this.addDatapoint();
            }
            
            
        })
    }  
    
    
 async addDatapoint() {
    const alert = await this.alertController.create({
        header: 'Add a Datapoint',
        inputs: [
            {
                name: 'name',
                type: 'text',
                placeholder: 'Name of Datapoint'
            },
            {
                name: 'initial',
                type: 'number',
                placeholder: 'Initial value'
            }
        ],
        buttons: [
            {
                text: 'Cancel',
                role: 'cancel',
                cssClass: 'secondary',
                handler: () => {
                }
            }, {
                text: 'Create',
                handler: (data) => {
                    this.datapoints.push({timeSeriesId:0, timeSeriesName: data.name, value:data.initial});
                }
            }
        ],
        mode: "md"
    });

    await alert.present();
  }   
  
  
    public formatDate(date){
        return moment(date).fromNow();
    }   
    
    public openLink(link){
        this.iab.create(link, '_system');
    } 

}
